.home{
    display: flex;
    flex-direction: column;
}

.home__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px;
}



.home__header a{
    margin-right: 20px;
    text-decoration: none;
    color: rgba(0,0,0,0.87);
    font-size: 13px;
}

.home__header a:hover{
    text-decoration: underline;
}

.home_headerRight,.home__headerLeft {
    display: flex;
    justify-content:space-between;
    align-items: center;
    min-width: 13vw;
}

.Home__body{
    flex:1;
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
}

.Home__body >img {
    height: 90px;
    object-fit: contain;
}